<template>
    <div class="input">
        <div class="overflow">
            <h2 class="left">New Service</h2>
        </div>
        <table>
            <tr>
                <td><label for="id-input">Name</label></td>
                <td><input id="id-input" type="text" autocomplete="off"
                           v-model="serviceConfiguration.id"/></td>
            </tr>
            <tr>
                <td><label for="category-input">Category</label></td>
                <td>
                    <select id="category-input" v-model="selectedCategory"
                            v-on:change="loadServiceTypes(selectedCategory)">
                        <option v-for="category in serviceCategories" v-bind:value="category.type"
                                v-bind:key="category.type">
                            {{category.label}}
                        </option>
                    </select>
                </td>
            </tr>
            <tr>
                <td><label for="type-input">Type</label></td>
                <td>
                    <select id="type-input" v-model="selectedType"
                            v-on:change="loadTypeParameters(selectedType)">
                        <option v-for="type in serviceTypes" v-bind:value="type.type" v-bind:key="type.type">
                            {{type.label}}
                        </option>
                    </select>
                </td>
            </tr>

            <template v-for="(param, index) in parameters"
                      v-bind:id="param.name"
                      v-bind:index="index">
                <tr v-bind:key="param.name">
                    <td>
                        <label v-if="param.optional">{{formatString(param.name)}}</label>
                        <label v-if="!param.optional">{{formatString(param.name)}}*</label>
                    </td>
                    <td v-if="param.type == 'int'">
                        <input :type="param.secured ? 'password' : 'text'" autocomplete="off"
                               v-model.number="parameterValues[index]"/>
                    </td>
                    <td v-if="param.type != 'int'">
                        <input :type="param.secured ? 'password' : 'text'" autocomplete="off"
                               v-model.trim="parameterValues[index]"/>
                    </td>
                </tr>
            </template>

        </table>
        <div v-if="showTestResult && testOk" class="feedbackbox">
            {{ testResult }}
        </div>
        <div v-if="showTestResult && !testOk" class="alertbox">
            {{ testResult }}
        </div>
        <div v-if="showSaveResult && !saveOk" class="alertbox">
            {{ saveResult }}
        </div>
        <button class="button right margin-left" @click="saveConfiguration()">
            <font-awesome-icon icon="save"/>
        </button>
        <button class="button right" @click="testConfiguration()">
            <font-awesome-icon icon="plug"/>
        </button>
    </div>
</template>

<script>
    export default {
        name: "NewService",
        data: function () {
            return {
                showTestResult: false,
                testResult: '',
                testOk: true,
                showSaveResult: false,
                saveResult: '',
                saveOk: true,
                serviceCategories: [],
                selectedCategory: '',
                serviceTypes: [],
                selectedType: '',
                parameters: [],
                parameterValues: [],
                serviceConfiguration: {
                    id: '',
                    type: '',
                    parameters: {}
                }
            }
        },
        methods: {
            loadServiceCategories: function () {
                var component = this;
                this.$http.get(this.$igorUrl + "/api/service/category")
                    .then(function (response) {
                        for (var i = component.serviceCategories.length; i > 0; i--) {
                            component.serviceCategories.pop();
                        }
                        Array.from(response.data).forEach(function (item, index) {
                            component.serviceCategories.push(item)
                        })
                        component.selectedCategory = component.serviceCategories[0].type;
                        component.loadServiceTypes(component.selectedCategory);
                    }).catch(function (error) {
                    console.log(error);
                })
            },
            loadServiceTypes: function (category) {
                var component = this;
                this.$http.get(this.$igorUrl + "/api/service/category/" + category)
                    .then(function (response) {
                        for (var i = component.serviceTypes.length; i > 0; i--) {
                            component.serviceTypes.pop();
                        }
                        Array.from(response.data).forEach(function (item, index) {
                            component.serviceTypes.push(item)
                        })
                        component.selectedType = component.serviceTypes[0].type;
                        component.loadTypeParameters(component.selectedType);
                    }).catch(function (error) {
                    console.log(error);
                })
            },
            loadTypeParameters: function (type) {
                var component = this;
                this.$http.get(this.$igorUrl + "/api/service/type/" + type)
                    .then(function (response) {
                        for (var i = component.parameters.length; i > 0; i--) {
                            component.parameters.pop();
                        }
                        Array.from(response.data).forEach(function (item, index) {
                            component.parameters.push(item)
                        })
                    }).catch(function (error) {
                    console.log(error);
                })
            },
            testConfiguration: function () {
                var component = this;
                component.serviceConfiguration.type = component.selectedType;
                this.parameters.forEach(function (param, index) {
                    component.serviceConfiguration.parameters[param.name] = component.parameterValues[index];
                })
                this.$http.post(this.$igorUrl + "/api/service/test", this.serviceConfiguration
                ).then(function (response) {
                    component.testResult = response.data;
                    component.showTestResult = true;
                    component.testOk = true;
                }).catch(function (error) {
                    component.testResult = error.response.data;
                    component.showTestResult = true;
                    component.testOk = false;
                });
            },
            saveConfiguration: function () {
                var component = this;
                component.serviceConfiguration.type = component.selectedType;
                this.parameters.forEach(function (param, index) {
                    component.serviceConfiguration.parameters[param.name] = component.parameterValues[index];
                })
                this.$http.post(this.$igorUrl + "/api/service", this.serviceConfiguration
                ).then(function (response) {
                    component.$emit('save')
                }).catch(function (error) {
                    component.saveResult = error.response.data;
                    component.showSaveResult = true;
                    component.saveOk = false;
                });
            },
            formatString: function (string) {
                string = string.replace(/\.?([A-Z])/g, function (x, y) {
                    return " " + y.toLowerCase()
                }).replace(/^_/, "");
                return string.charAt(0).toUpperCase() + string.slice(1)
            }
        },
        mounted() {
            this.loadServiceCategories();
        }
    }
</script>

<style scoped>

    .input {
        background-color: var(--panel-background-color);
        padding: 15px;
        overflow: hidden;
        border-bottom: 5px solid var(--main-background-color);
    }

    .input table tr {
        line-height: 30px;
    }

    .input table td {
        text-align: left;
        padding-right: 15px;
    }

    .input table tr td label {
        color: var(--font-color-light);
        margin-right: 15px;
    }

    .input table tr td input, select {
        border: none;
        background-color: var(--element-background-color);
        height: 25px;
        min-width: 300px;
        color: var(--font-color-light);
    }

    .input table tr td input {
        padding-left: 5px;
    }

    .input table tr td select option {
        border: none;
        outline: none;
        background-color: var(--element-background-color);
        height: 25px;
    }

    .input table tr td select:focus,
    .input table tr td input:focus {
        background-color: var(--element-background-color-focus);
        color: var(--panel-background-color);
        outline: none;
    }

</style>