<template>
    <div class="panel">
        <div class="header-container overflow">
            <h1>Available Services</h1>
            <button class="button right" @click="showNewService = !showNewService">
                <font-awesome-icon icon="plus" v-if="showNewService == false"/>
                <font-awesome-icon icon="minus" v-if="showNewService == true"/>
            </button>
        </div>

        <transition name="fade">
            <NewService v-if="showNewService" v-on:save="loadExistingServices"/>
        </transition>

        <ServiceListEntry v-for="(service, index) in services"
                          v-bind:id="service"
                          v-bind:index="index"
                          v-bind:key="service"/>
    </div>
</template>

<script>
    import NewService from "./NewService";
    import ServiceListEntry from "./ServiceListEntry";

    export default {
        name: "ServiceList",
        components: {ServiceListEntry, NewService},
        data: function () {
            return {
                services: [],
                showNewService: false,
            }
        },
        methods: {
            loadExistingServices: function () {
                var component = this;
                this.$http.get(this.$igorUrl + "/api/service/ids")
                    .then(function (response) {
                        for (var i = component.services.length; i > 0; i--) {
                            component.services.pop();
                        }
                        Array.from(response.data).forEach(function (item, index) {
                            component.services.push(item)
                        })
                        component.services.sort((a, b) => a.localeCompare(b));
                        component.showNewService = false;
                    }).catch(function (error) {
                    console.log(error);
                });
            }
        },
        mounted() {
            this.loadExistingServices()
        }
    }
</script>

<style scoped>

    .header-container {
        padding: 15px 15px 15px 15px;
        border-bottom: 5px solid var(--main-background-color);
    }

</style>