<template>
    <div>
        <div class="navigation">
            <ul>
                <li v-bind:class="[isActiveTab('services') ? activeClass : '', tabClass]"><a
                        v-on:click="setActiveTab('services')">Services</a></li>
                <li v-bind:class="[isActiveTab('jobs') ? activeClass : '', tabClass]"><a
                        v-on:click="setActiveTab('jobs')">Jobs</a></li>
            </ul>
        </div>
        <div class="center">
            <ServiceList v-if="isActiveTab('services')"/>
        </div>
    </div>
</template>

<script>
    import ServiceList from "./service/ServiceList";

    export default {
        name: "Navigation",
        components: {ServiceList},
        data: function () {
            return {
                choice: 'services',
                activeClass: 'active',
                tabClass: 'tab'
            }
        },
        methods: {
            setActiveTab: function (val) {
                this.choice = val;
            },
            isActiveTab: function (val) {
                return this.choice === val;
            }
        }
    }
</script>

<style scoped>
    .navigation {
        height: 35px;
        background-color: var(--nav-background-color);
        margin-bottom: 5px;
    }

    .tab {
        display: inline-block;
        cursor: pointer;
        font-size: 150%;
        color: var(--font-color-light);
        margin: 5px 15px 0px 10px;
        padding: 5px 15px 5px 15px;
    }

    .active {
        font-weight: bold;
        background-color: var(--panel-background-color);
        margin: -5px 15px 0px 10px;
        padding: 15px 15px 5px 15px;
    }
</style>